* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  font-family: sans-serif;
  background: lightslategray;
  
}

.settings {
  background: lightcyan;
  width: 500px;
  padding: 20px;
  margin-top: 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
}

.text {
  width: 250px;
  height: 40px;
  margin: 5px;
  font-size: 30px;
  outline: none;
  border: none;
  border-radius: 6px;
  padding-left: 10px;
}

.inner {
  text-align: center;
}

input[type="file"] {
  display: none;
}

.file {
  border: 2px solid lightslategray;
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 7px;
}

.file:hover {
  color: white;
  background: lightslategray;
}

.colorSettings {
  padding: 5px;
}

.colorSettings label {
  font-size: 20px;
}

.meme {
  width: 300px;
  padding: 20px;
  border-radius: 10px;
}

.meme h3 {
  font-size: 30px;
}

.meme img {
  padding: 10px;
  border-radius: 10px;
}

.outMeme {
  display: flex;
  justify-content: center;
}

.memeAndDownload {
  text-align: center;
  padding-top: 20px;
}

.download {
  width: 150px;
  height: 35px;
  margin-top: 10px;
  border-radius: 6px;
  background: none;
  border: 1px solid lightcyan;
  font-size: 20px;
  color: lightcyan;
}

.download:hover {
  background: lightcyan;
  color: lightslategray ;
}

.button {
  background: lightslategray;
  width: 120px;
  height: 30px;
  border-radius: 6px;
  border: 2px solid lightslategray;
  color:white;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  padding-top: 20px;
  color: lightcyan;
}
